import React from 'react';
import './Main.css';
import MyStore from './MyStore';
import imageClick from './myScript';

MyStore.sort( () => {
    return Math.random() - 0.5;
})
console.log(MyStore)
const new_MyStore = MyStore.slice(0, 15);

export default class Main extends React.Component{   
    render(){    
        let images = new_MyStore.map((el) => {
            return `images/${el}`;    
        })
    
        images = images.concat(images);
    
        images.sort( () => {
            return Math.random() - 0.5;
        })

        // let state = {
        //     images
        // }
        // console.log(state)
        let MainItems = () => {
            let key = 0;
            return(
                images.map(function(i){
                    key++;
                    return(
                        <div key={key} className='grid_item'><img src={i} alt="not found"
                         className='hidden' onClick={imageClick} id={key}/></div>
                    )            
                })
            )
        }

        return(
            <div className='main'>
                <MainItems />            
            </div>       
        )
    }
    
}